.profile__title {
    margin: 0;
    padding: 0;
    font-weight: 500;
    font-size: 42px;
    line-height: 114%;
    color: #FFFFFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 767px) {
    .profile__title {
        text-align: center;
        font-size: 27px;
        line-height: 33px;
    }
}