.profile__subtitle {
    margin: 0;
    padding-top: 14px;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #FFFFFF;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
}

@media screen and (max-width: 767px) {
    .profile__subtitle {
        text-align: center;
        font-size: 14px;
        line-height: 17px;
    }
}